<template>
  <div class="d-flex align-content-around flex-wrap p-2">
    <div v-for="item in cardItems" class="card aligned-self-start m-2" style="width: 20em;">
      <img v-if="item.cardImg != undefined && item.cardImg.pos == 'top'" class="card-img-top" :src="cardImg.src" alt="Card image cap">
      <div class="card-body">
        <h5 class="card-title"><a :href="'#' + item.route">{{ item.title }}</a></h5>
        <h6 class="card-subtitle mb-2">{{ item.subtitle }}</h6>
        <p class="card-text">
          {{ item.text }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cardItems: [
        {
          route: '/profiles',
          title: 'Profiles',
          subtitle: 'Profiles are containers for configurations',
          text: 'Using profiles you can create different set of settings for your instances. For example a production and a test environment.',
        },
        {
          route: '/instances',
          title: 'Instances',
          subtitle: 'Servers and clients',
          text: 'Here you can configure, start and stop servers and clients. Also you can navigate through their configuration trees or send commands to.',
        },
        {
          route: '/media',
          title: 'Media',
          subtitle: 'Add and update media sources',
          text: 'Here you can add new sources for third party media repositories. Also you can update these repositories.',
        },
        {
          route: '/servers',
          title: 'Server Network',
          subtitle: 'Locate server providers and their provided servers',
          text: 'Here you add and update the list of server providers. Also you can start the client and instantly connect to the selected server.',
        },
        {
          route: '/interfaces',
          title: 'Interfaces',
          subtitle: 'Manage the user interfaces',
          text: 'Here you add and update the user interfaces like the HUD or a third party admin panel.',
        },
        {
          route: '/info',
          title: 'Information',
          subtitle: 'Information about the server.',
          text: 'You can find system information and logs here.',
        },
        {
          route: '/releases',
          title: 'Releases',
          subtitle: 'Manage Inexor-releases',
          text: 'See and manage old and new inexor releases.',
        },
        {
          route: '/logging',
          title: 'Logging',
          subtitle: 'Configure log levels',
          text: 'In case you have to debug you can adjust the log levels.',
        },
        {
          route: 'https://www.inexor.org/',
          title: 'Inexor',
          subtitle: 'An open-source first person shooter forked from Cube 2',
          cardImg: {
            position: 'bot',
            src: '../assets/inexor-logo.svg',
            alt: 'Inexor Logo',
          },
          text: 'For further information about Inexor check out the brand new website!',
        },
      ],
    };
  },
};
</script>



