<template>
  <div id="app" class="container-fluid p-0">
    <nav class="navbar navbar-light bg-light navbar navbar-expand-lg navbar-light bg-light">
      <a class="navbar-brand" href="#">
        <img src="/static/img/inexor-logo.svg" width="30" height="30" class="d-inline-block align-top" alt="Inexor Logo">
        Inexor
      </a>
      <div class="collapse navbar-collapse">
        <ul class="navbar-nav mr-auto">
          <li v-for="item in navItems" v-bind:class="{ 'active': item.active, 'list-item': true }">
            <a v-bind:href="'#' + item.route" class="nav-link">{{ item.label }}</a>
          </li>
        </ul>
      </div>
    </nav>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  created() {
    this.updateActiveRoute();
    window.addEventListener('hashchange', this.updateActiveRoute.bind(this));
  },
  methods: {
    updateActiveRoute() {
      let haveActive = false;
      for (let i = this.navItems.length - 1; i >= 0; i -= 1) {
        if ((i === 0 && !haveActive) || window.location.hash.includes(this.navItems[i].route)) {
          this.navItems[i].active = true;
          haveActive = true;
        } else {
          this.navItems[i].active = false;
        }
      }
    },
  },
  data() {
    return {
      navItems: [
        {
          route: '/profiles',
          label: 'Profiles',
          active: false,
        },
        {
          route: '/instances',
          label: 'Instances',
          active: false,
        },
        {
          route: '/media',
          label: 'Media',
          active: false,
        },
        {
          route: '/interfaces',
          label: 'Interfaces',
          active: false,
        },
        {
          route: '/releases',
          label: 'Releases',
          active: false,
        },
        {
          route: '/logging',
          label: 'Logging',
          active: false,
        },
        {
          route: '/info',
          label: 'Information',
          active: false,
        },
      ],
    };
  },
};
</script>



